import styles from "./index.scss"
import Page from "../components/page"

const Home = () => {
    return <Page>
        <header className={styles.header} id="top">
            <a href="#top" className={styles.avatar} alt="Systemcluster"></a>
            <div className={styles.title}>
                <span>
                <span>Systemcluster</span>
                </span>
            </div>
            <nav className={styles.outbound}>
                <ul>
                    <li>
                        <a description="Projects" href="#projects" aria-haspopup="true"><i className="fas fa-briefcase"></i></a>
                    </li>
                    <li>
                        <a description="Articles" href="#security" aria-haspopup="true"><i className="fas fa-newspaper"></i></a>
                    </li>
                    <li>
                        <a description="Contact / Email" href="#contact" aria-haspopup="true"><i className="fas fa-envelope"></i></a>
                    </li>
                    <li>
                        <a href="//github.com/Systemcluster" description="Github" target="_blank" aria-haspopup="true"><i className="fab fa-github"></i></a>
                    </li>
                    <li>
                        <a description="About" href="#about" aria-haspopup="true"><i className="fas fa-question"></i></a>
                    </li>
                </ul>
            </nav>
        </header>
        <main className={styles.main}>
            <nav className={styles.outbound}>
                <ul>
                    <li>
                        <a description="Projects" href="#projects" aria-haspopup="true"><i className="fas fa-briefcase"></i></a>
                    </li>
                    <li>
                        <a description="Articles" href="#articles" aria-haspopup="true"><i className="fas fa-newspaper"></i></a>
                    </li>
                    <li>
                        <a description="Contact / Email" href="#contact" aria-haspopup="true"><i className="fas fa-envelope"></i></a>
                    </li>
                    <li>
                        <a href="//github.com/Systemcluster" description="Github" target="_blank" aria-haspopup="true"><i className="fab fa-github"></i></a>
                    </li>
                    <li>
                        <a description="About" href="#about" aria-haspopup="true"><i className="fas fa-question"></i></a>
                    </li>
                </ul>
            </nav>
            <div className={styles.content}>
                <h2><span>Projects</span></h2>
                <section>
                Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
                </section>
                <h2><span>Contact</span></h2>
                <h2><span>Security</span></h2>
                <h2><span>About</span></h2>
            </div>
        </main>
    </Page>
}

export default Home
